@import url(https://fonts.googleapis.com/css2?family=Rubik&display=swap);
h1 {
  padding: 0.5rem 1rem;
}

body {
  margin: 0;
}

.login-page {
  text-align: center;
}

.spotifyBtn {
  margin: 0 auto;
  display: block;
  margin: auto;
  text-align: center;
  text-align: center;
  display: inline-block;
  justify-content: center;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.masonry-with-columns {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  max-height: 3000px;
  max-width: 100%;
}
.masonry-with-columns div {
  flex: 1 0 auto;
  margin: 0 0.5rem 1rem 0.5rem;
  text-align: center;
  font-weight: 900;
  font-size: 2rem;
}

.masonry-with-columns div * {
  max-width: 18rem;
}

.masonry-with-columns div img {
  margin-left: 0 0.5rem;
}

/* add css module styles here (optional) */

._3xrlJ {
  transition-duration: 300ms;
  font-family: 'Rubik', Arial, Helvetica, sans-serif;
  background-color: #1db954;
  color: #fff;
  cursor: pointer;
  padding: 0.25rem 0.5rem;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 32px;
  border: none;
  border-radius: 0.2rem;
}

._3xrlJ:hover, ._3xrlJ:focus {
  transition-duration: 500ms;
  transform: translate(-1px, 2px);
  box-shadow: 0 0.25rem 0.75rem 0 #777777;
}

._1JCP_ {
  /* Convert spotify logo to white */
  -webkit-filter: invert(100%) sepia(3%) saturate(0%) hue-rotate(162deg)
    brightness(103%) contrast(103%);
          filter: invert(100%) sepia(3%) saturate(0%) hue-rotate(162deg)
    brightness(103%) contrast(103%);
  padding: 0.25rem 0.5rem;
  height: 1.5rem;
}

